<template>
  <div class="d-flex flex-column vh-100">
    <div class="page px-3 py-4">
      <transition>
        <account-info
          v-if="showAccountInfo"
          @showContact="showContactInfo=true;showAccountInfo=false"
          @showThankyou="showThankInfo=true;showAccountInfo=false"
          @showProduct="showProductInfo=true;showAccountInfo=false"
        />
      </transition>
      <transition>
        <contact-info
          v-if="showContactInfo"
          @showAccountInfo="showAccountInfo=true;showContactInfo=false"
        />
      </transition>
      <manage-product v-if="showProductInfo" 
      @showAccountInfo="showAccountInfo=true;showProductInfo=false"
      />
      <thank-you-message v-if="showThankInfo" 
       @showAccountInfo="showAccountInfo=true;showThankInfo=false"/>
      <manage-order v-if="showOrderInfo" />
    </div>
    <bottom-navigation-bar />
  </div>
</template>

<script>
import BottomNavigationBar from "../components/BottomNavigationBar";
//import ThankYouMessage from './seller/ThankYouMessage';
export default {
  components: {
    BottomNavigationBar,
    "account-info": () => import("./seller/AccountInfo"),
    "contact-info": () => import("./seller/ContactInfo"),
    "manage-product": () => import("./seller/ManageProduct"),
    "thank-you-message": () => import("./seller/ThankYouMessage"),
    "manage-order": () => import("./seller/ManageOrder")
    //ThankYouMessage
  },
  data() {
    return {
      showAccountInfo: true,
      showContactInfo: false,
      showProductInfo: false,
      showThankInfo: false,
      showOrderInfo: false
    };
  }
};
</script>
<style lang="scss">
@import "@/scss/_PageSetup.scss";
@import "@/scss/_FontEmbeds.scss";
@import "@/scss/_Utilities.scss";
</style>
<style lang="scss" scoped>
.page {
  flex: 1;
  overflow-y: auto;
}
</style>